@tailwind base;
@tailwind components;
@tailwind utilities;

@keyframes fadeIn {
    0% {
      opacity: 0;
      transform: translateY(-10px);
    }
    100% {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .animate-fadeIn {
    opacity: 0;
    animation: fadeIn 0.3s forwards;
  }

  .projects-container {
    position: sticky; 
    top: 0; 
    height: 400vh;
  }
  
  .project-item {
    height: 700px;
    position: absolute;
    width: 110%;
    top: 0;
    left: 0;
    transform: translateY(0);
    transition: transform 0.8s ease-in-out;
  }
  
  .sticky {
    position: sticky;
    top: 0;
    z-index: 1;
  }
  
  .show {
    transform: translateY(0); 
    z-index: 2;
  }
  